@use 'base/mixins' as m;
@use 'base/variables' as v;

.citas {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    @include m.tablet { 
        @include m.grid(2, 1rem); 
    }
    @include m.desktop { 
        @include m.grid(3, 1rem); 
    }
    li {
        background-color: v.$gris;
        border-radius: .5rem;
        padding: 1.5rem;
        border-top: 4px solid v.$gris-oscuro;
        border-bottom: 4px solid v.$gris-oscuro;
        transition: all ease 1s;
        margin-top: 2.5rem;
        flex: 1;
        p {
            color: v.$rosa;
            font-size: 1.6rem;
            font-weight: 900;
        }
        span {
            color: v.$negro;
        }
        h3 {
            font-weight: 900;
            color: v.$rosa;
            font-size: 3.5rem;
            padding: 2rem 0 1rem 0;
        }
    }
}
.dialibre {
    h2 {
        margin-top: 5rem;
        margin-bottom: 3rem;
        color: v.$rosa;
        font-weight: 900;
        font-size: 2.5rem;
        @include m.tablet { 
            margin-top: 10rem;
            font-size: 3rem;
        }
    }
}
.serviciosadm {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 2rem;
    @include m.tablet { 
        @include m.grid(2, 1rem);   
        text-align: justify;
    }
    @extend .citas;
    li {
        border-top: 4px solid v.$gris-oscuro;
        border-bottom: 4px solid v.$gris-oscuro;
        &:first-of-type {
            border-top: 4px solid v.$gris-oscuro;
            border-bottom: 4px solid v.$gris-oscuro;
        }
    }
    .acciones {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 1rem;
        padding: 1rem 0 2rem 0;
        a {
            color: v.$blanco;
            font-family: v.$fparrafos;
        }
        .boton-acciones {
            width: auto;
        }
        .boton-accionesb {
            width: auto;
        }
    }
}