@use 'base/mixins' as m;
@use 'base/variables' as v;

.actualizarservicio {
    min-height: 100vh;
    padding: 4rem 0;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
    .cita {
        &__descripcion {
            color: v.$negro;
        }
    }
    .formulario {
        margin-bottom: 2rem;
    }
    .footer {
        &__titulo {
            color: v.$negro;
        }
    }
}