@use 'base/variables' as v;
@use 'base/mixins' as m;

.servicios {
    @include m.tablet { 
        @include m.grid(4, 1rem);   
    }
}
.servicio {
    background-color: v.$gris;
    text-align: center;
    font-weight: bold;
    padding: 2rem;
    border-radius: .5rem;
    transition: all ease .8s;
    margin-bottom: 1rem;
    &:last-of-type {
        margin-bottom: 0;
    }
    @include m.tablet { 
        margin-bottom: 0;   
    }
    &:hover {
        cursor: pointer;
        background-color: darken(v.$gris, 5%);
        transform: scale(0.9);
    }
    &__nombre {
        color: v.$negro;
        font-family: v.$fprincipal;
    }
    &__precio {
        color: v.$rosa;
        font-family: v.$fcursiva;
        font-size: 2rem;
    }
    &__seleccionado {
        background-color: v.$azul;
        &:hover {
            background-color: darken(v.$azul, 5%);
        }
        p {
            color: v.$blanco;
        }
    }
}
