@use 'base/variables' as v;
@use 'base/mixins' as m;

.seccion {
    &__heading {
        font-family: v.$fprincipal;
        color: v.$rosa;
        font-weight: 900;
        font-size: 3rem;
    }
    &__servicio {
        border-bottom: 1px solid v.$negro;
        padding: 1rem 0;
        font-weight: 900;
        &:last-of-type {
            margin-bottom: 2rem;
        }      
    }
    &__resumen {
        p {
            font-weight: 900;
            color: v.$negro;
            span {
                color: v.$azul;
            }
        }
    }
    &__boton {
        @include m.boton(v.$rosa, v.$blanco);
        width: 20rem;
        border-radius: .3rem;
        margin: 0 auto;
        margin-bottom: 3rem;
        padding: 1rem;
    }
}