@use 'base/variables' as v;
@use 'base/mixins' as m;

.crud-volver {
    @include m.boton(v.$azul, v.$blanco);
    border-radius: 0;
    margin-bottom: 1rem;
    margin-left: auto;
    width: 15rem;
    @include m.tablet { 
        width: 15rem;
        margin-left: 0;
    }
}
.boton-crud {
    @include m.boton(v.$rosa, v.$blanco);
    width: 20rem;
    border-radius: .3rem;
    margin: 0 auto;
    margin-top: 3rem;
    padding: 1rem;
}
.boton-acciones {
    @include m.boton(v.$rosa, v.$blanco);
    border-radius: 0;
    width: 25rem;
}
.boton-accionesb {
    @include m.boton(v.$rojo, v.$blanco);
    border-radius: 0;
    width: 25rem;
}
.boton-volver {
    @include m.boton(v.$azul, v.$blanco);
    width: 15rem;
    margin-bottom: 2rem;
    border-radius: 0;
    @include m.tablet { 
        width: 15rem;
    }
}
.boton-delete {
    @include m.boton(v.$rojo, v.$blanco);
    width: 15rem;
    margin-bottom: 2rem;
    margin-left: auto;
    border-radius: 0;
    @include m.tablet { 
        width: 15rem;
    }
}