@use 'base/mixins' as m;
@use 'base/variables' as v;

.correo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
        margin-bottom: 5rem;
    }
    .mensaje {
        &__texto {
            padding: 0;
            margin-bottom: 5rem;
        }
        &__heading {
            font-family: v.$fprincipal;
            color: v.$rosa;
            font-weight: 900;
            font-size: 3.3rem;
        }
        &__descripcion {
            font-family: v.$fsecundaria;
            color: v.$negro;
            text-align: center;
            font-weight: 600;
            font-size: 1.8rem;
        }
    }
    &__contenedor {
        @include m.desktop { 
            width: 85%;
        }
    }
}