@use 'base/mixins' as m;
@use 'base/variables' as v;

.citaseccion {
    min-height: 100vh;
    padding: 4rem 0;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
    .formulario {
        label {
            color: v.$negro;
        }
        input {
            background-color: v.$gris;
            border: none;
            color: v.$negro;
            &:disabled {
                background-color: v.$rosa;
                color: v.$negro;
            }
        }
    }
}
.cita {
    &__heading {
        font-family: v.$fprincipal;
        color: v.$rosa;
        font-weight: 900;
        font-size: 3.5rem;
    }
    &__descripcion {
        text-align: center;
        color: v.$negro;
        font-weight: 700;
        font-family: v.$fparrafos;
        font-size: 1.8rem;
    }
}
.seccion {
    padding-top: 2rem;
    display: none;
    &__titulo {
        text-align: center;
        font-size: 3.5rem;
    }
    &__descripcion {
        text-align: center;
        color: v.$negro;
        font-weight: 700;
        font-family: v.$fparrafos;
        font-size: 1.8rem;
    }
}
.mostrar {
    display: block;
}
.ocultar {
    display: none;
}
