@use 'base/mixins' as m;
@use 'base/variables' as v;

.grid-auth {
    width: 100%;
    &__imagen {
        @include m.tablet { 
            background-image: url(../img/logo2.png);
            background-size: 27rem 37rem;
            background-position: center center;
            background-repeat: no-repeat;
            -webkit-box-shadow: 17px 10px 14px 0px rgba(225,225,225,1);
            -moz-box-shadow: 17px 10px 14px 0px rgba(225,225,225,1);
            box-shadow: 17px 10px 14px 0px rgba(225,225,225,1);
        }
        @include m.desktop { 
            background-size: 37rem 47rem;
        }
    }
    &__contenido {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100vh;
        overflow-y: scroll;
        overflow-x: hidden;
        -webkit-box-shadow: -17px 10px 14px 0px rgba(225,225,225,1);
        -moz-box-shadow: -17px 10px 14px 0px rgba(225,225,225,1);
        box-shadow: -17px 10px 14px 0px rgba(225,225,225,1);
    }
    @include m.tablet { 
        @include m.grid1-2(0); 
    } 
}

.admin {
    min-height: 100vh;
    padding: 4rem 0;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
        @include m.tablet { 
            padding: 0;
            background-position: center top;
        }
    }
    .busqueda {
        &__heading {
            color: v.$negro;
            margin-bottom: 2rem;
        }
    }
    .cita {
        &__heading {
            color: v.$rosa;
            font-size: 3.5rem;
        }
        &__descripcion {
            text-align: center;
            font-weight: 700;
            font-size: 1.8rem;
            color: v.$negro;
        }
    }
    .layoutb {
        margin-bottom: 2rem;
    }
    .formulario {
        input {
            background-color: v.$gris;
            color: v.$negro;
            border: none;
        }
    }
    .footer {
        &__titulo {
            color: v.$negro;
        }
    }
}