@use 'base/mixins' as m;
@use 'base/variables' as v;

.accionesauth {
    display: flex;
    flex-direction: column;
    text-align: center;
    column-gap: 1.5rem;
    padding: 5rem 0 2rem 0;
    @include m.tablet { 
        flex-direction: row;
        justify-content: space-between;
    }
    a {
        @include m.headCursiva(v.$rosa);
        margin-bottom: 3rem;
        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet { 
            margin-bottom: 0;   
        }   
    }
}