@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario {
    width: 100%;
    &__campo {
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;
        margin-top: 1rem;
        @include m.tablet { 
            flex-direction: row;
            align-items: center;
        }
        label {
            font-size: 1.8rem;
            font-weight: 600;
            margin-bottom: 1rem;
            color: v.$negro;
            @include m.tablet { 
                font-weight: 900;
                flex: 0 0 12rem;
                margin-bottom: 0;
                
            }
        }
        input {
            border: 1px solid v.$gris;
            padding: 1rem;
            background-color: unset;
            color: v.$negro;
            font-weight: 700;
            @include m.tablet { 
                flex: 1;
            }
            &:disabled {
                color: v.$negro;
                font-weight: 900;
                cursor: not-allowed;
                @include m.tablet { 
                    background-color: v.$rojo;
                }
            }
        }
    }
    &__boton {
        @include m.boton(v.$rosa, v.$blanco);
        margin: 0 auto;
        margin-top: 2.5rem;
        border-radius: 0;
        @include m.tablet { 
            width: 25rem;   
        }
    }
}