@use 'base/mixins' as m;
@use 'base/variables' as v;

.alerta {
    text-align: center;
    margin-bottom: .5rem;
    padding: 1rem;
    font-weight: 900;
    text-transform: uppercase;
    color: v.$blanco;
    &.error {
        background-color: v.$rojo;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
    &.exito {
        background-color: v.$verde;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
.tabs {
    @include m.tablet { 
        display: flex;
    }
    button {
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: 800;
        transition: all ease 400ms;
        &:hover {
            cursor: pointer;
            background-color: darken(v.$azul, 15%);
        }
        &.actual {
            background-color: v.$gris;
            color: v.$azul;
        }
    }
}
.paginacion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 2.5rem;
}
.ocultarb {
    visibility: hidden;
}