@use 'base/mixins' as m;
@use 'base/variables' as v;

.grid {
    width: 100%;
    &__imagen {
        @include m.tablet { 
            background-image: url(../img/logo2.png);
            background-size: 35rem 45rem;
            background-position: center center;
            background-repeat: no-repeat;
        }
        @include m.desktop { 
            background-size: 50rem 60rem;
        }
    }
    &__contenido {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100vh;
    }
    @include m.tablet { 
        @include m.grid(2, 0); 
    } 
}

.olvide,
.login,
.recupera,
.registrar {
    padding: 1rem;
    &__texto {
        padding: 0;
    }
    &__heading {
        font-family: v.$fprincipal;
        color: v.$rosa;
        font-weight: 900;
        font-size: 3.5rem;
    }
    &__descripcion {
        font-family: v.$fsecundaria;
        color: v.$negro;
        text-align: center;
        font-weight: 600;
        font-size: 1.8rem;            
    }
    &__contenedor {
        width: 85%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
}