@use 'base/mixins' as m;
@use 'base/variables' as v;

.miscitas {
    min-height: 100vh;
    padding: 4rem 0;
    &__contenedor {
        display: flex;
        flex-direction: column;
        min-height: 90vh;
    }
    .datosuser {
        &__nombre {
            font-family: v.$fprincipal;
            font-size: 3.5rem;
            font-weight: 900;
            color: v.$rosa;
            margin-bottom: 2rem;
        }
        &__descripcion {
            text-align: center;
            font-size: 1.8rem;
            font-family: v.$fparrafos;
            color: v.$negro;
            margin-top: 2rem;
            font-weight: 700;
            padding: 0 0 1rem 0;
        }
    }
    .datoscita { 
        .citauser {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            @include m.tablet { 
                @include m.grid(3, 1rem);
            }
            p {
                &.nota {
                    color: v.$azul;
                }
            }
            li{
                background-color: v.$gris;
                padding: 1rem;
                text-align: center;
                &:last-of-type {
                    margin-bottom: 2rem;
                    @include m.tablet { 
                        margin-bottom: 0;
                    }
                }
                p {
                    font-size: 2rem;
                    font-family: v.$fsecundaria;
                    color: v.$rosa;
                    font-weight: 700;
                    span {
                        color: v.$negro;
                        font-size: 1.8rem;
                    }
                }
            }
        }
        .acciones {
            .boton-accionesb {
                width: 30rem;
                font-weight: 900;
                margin: 0 auto;
            }
        }
    }
    .footer {
        &__titulo{
            color: v.$negro;
        }
    }
}