@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra {
    font-weight: 900;
    display: flex;
    flex-direction: column;
    @include m.tablet { 
        justify-content: space-between;
        align-items: flex-end;
    }
    p {
        @include m.boton(v.$rosa, v.$blanco);
        padding: 1rem;
        font-size: 1.8rem;
        border-radius: .5rem;
        margin-bottom: .5rem;
        cursor: not-allowed;
        @include m.tablet {  
            min-width: 20rem; 
        }
    }
    .mis-citas {
        @include m.boton(v.$azul, v.$blanco);
        padding: 1rem;
        font-size: 1.8rem;
        border-radius: .5rem;
        margin-bottom: .5rem;
        @include m.tablet { 
            min-width: 20rem;
        }
    }
    &__logout {
        @include m.boton(v.$rosa, v.$blanco);
        padding: 1rem;
        font-size: 1.8rem;
        border-radius: .5rem;
        margin-bottom: 2rem;
        @include m.tablet { 
            min-width: 20rem;
        }
    }
    &__secciones {
        @include m.desktop { 
            display: flex;
            gap: 1rem;
            justify-content: space-between;   
        }
    }
    &__botones {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        margin-bottom: 1rem;
        border-radius: 0;
        @include m.desktop { 
            flex: 1;
        }
    }
}
