@use 'base/mixins' as m;
@use 'base/variables' as v;

.confirma {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
        margin-bottom: 5rem;
    }
    a {
        margin-top: 5rem;
    }
    .confirmaste {
        &__heading {
            font-family: v.$fprincipal;
            color: v.$rosa;
            font-weight: 900;
            font-size: 3.3rem;
        }
    }
    &__contenedor {
        width: 85%;
    }
}